const defaultCategories = {
  entertainment: [
    "games",
    "movies",
    "sports",
    "other"
  ],
  investing: [
    "retirement",
    "betterment",
    "other"
  ]
}

export default function() {
  Object.entries(defaultCategories).reduce((categoryAcc, categoryCur) => {
    const [category, subcategories] = categoryCur;
    
    categoryAcc[category] = subcategories.reduce((subcatgoryAcc, subcategoryCur) => {
      subcatgoryAcc[subcategoryCur] = 0;
      return subcatgoryAcc;
    }, {})
    return categoryAcc;
  }, {})
}



