import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import SpendingForm from './spending-form';
import defaultCategories from './spending-form/default-categories';

class SpendingChart extends Component {
  render() {
    return <svg height="360" width="360" ></svg>
  }
}

class App extends Component {  
  constructor() {
    super();
    this.state = defaultCategories();
  }
  
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
        <SpendingForm data={this.state}/>
        <SpendingChart data={this.state}/>
      </div>
    );
  }
}

export default App;



